.layout {
    main.main {
        // min-height: 600px;
        // @include padding($gutter-half null);
        padding-bottom: $gutter-half;
        .container {}
    }
}

.container {
    @include padding($gutter-half);
    width: 100%;
    box-sizing: border-box;
}

//-----  MEDIA QUERY / RESPONSIVE ----
// only mobile : from 0 to 767
@media #{$bp-max-sm} {}

@media #{$bp-only-tablet} {}

@media #{$bp-min-md} {}

@media #{$bp-min-lg} {
    .container {
        width: 1200px;
        margin: 0 auto;
    }
}